<template>
  <div class="page-article" v-if="model">
    <div class="d-flex py-3 px-2 border-bottom">
      <div class="iconfont icon-Back" @click="$router.go(-1);"></div>
      <strong class="flex-1">
        {{ model.title }}
      </strong>
      <div class="text-grey">2019-06-12</div>
    </div>
    <div v-html="model.body" class="py-3 body fs-lg"></div>
    <div>
      <div>
        <i class="iconfont icon-menu1"></i>
        <span class="text-blue fs-lg ml-1">相关资讯</span>
      </div>
    </div>
    <div class="pt-1">
      <router-link
        class="py-2"
        :to="`/articles/${item._id}`"
        tag="div"
        v-for="item in model.related"
        :key="item._id"
      >
        {{ item.title }}
      </router-link>
    </div>
  </div>
</template>

<script>
export default {
  props: {
    id: { require: true },
  },
  data() {
    return {
      model: null,
    };
  },
  methods: {
    async fetch() {
      const res = await this.$http.get(`/articles/${this.id}`);
      this.model = res.data;
    },
  },
  created() {
    this.fetch();
  },
  watch:{
      id(){
          this.fetch()
      }
  }
};
</script>

<style lang="scss">
.page-article {
  .icon-Back {
    font-size: 1.692308rem;
  }
  .body {
    img {
      max-width: 100%;
      height: auto;
    }
    iframe {
      width: 100%;
      height: auto;
    }
  }
}
</style>