<template>
  <div class="home">
    <swiper ref="mySwiper" :options="swiperOptions">
      <swiper-slide>
        <img
          class="w-100"
          src="../assets/images/9809fa647d514579fcfc3c90d75fa8d0.jpeg"
          alt=""
        />
      </swiper-slide>
      <swiper-slide>
        <img
          class="w-100"
          src="../assets/images/cc1ecd960280bceb871baaaeaab5e9ea.jpeg"
          alt=""
        />
      </swiper-slide>
      <swiper-slide>
        <img
          class="w-100"
          src="../assets/images/735b9ff4f494bef7e7ef75a0a870b76b.jpeg"
          alt=""
        />
      </swiper-slide>
      <div
        class="swiper-pagination pagination-home text-right px-3 pb-1"
        slot="pagination"
      ></div>
    </swiper>
    <!-- end of swiper -->
    <div
      class="
        nav-icons
        bg-white
        mt-3
        d-flex
        flex-wrap
        text-center
        pt-3
        text-dark-1
      "
      style="overflow: hidden"
    >
      <router-link
        tag="div"
        to="/test"
        class="nav-item mb-3"
        v-for="n in 8"
        :key="n"
      >
        <i class="sprite sprite-news"></i>
        <div class="py-2">爆料站</div>
      </router-link>
    </div>
    <div class="bg-light py-2 fs-sm text-center" @click="changeHeight">
      <i class="sprite sprite-arrow mr-1"></i>
    </div>

    <m-list-card icon="menu1" title="新闻资讯" :categories="newsCats">
      <template #items="{ category }">
        <router-link
          :to="`/articles/${news._id}`"
          tag="div"
          class="py-2 fs-lg d-flex"
          v-for="(news, i) in category.newsList"
          :key="i"
        >
          <span class="text-info">[{{ news.categoryName }}]</span>
          <span class="px-2">|</span>
          <span class="flex-1 text-dark text-ellipsis">{{ news.title }}</span>
          <span class="text-grey">{{ news.createdAt | date }}</span>
        </router-link>
      </template>
    </m-list-card>
    <m-list-card icon="card-hero" title="英雄列表" :categories="heroCats">
      <template #items="{ category }">
        <div class="d-flex flex-wrap" style="margin: 0 -0.5rem">
          <router-link
            :to="`/heros/${hero._id}`"
            tag="div"
            class="p-2 text-center"
            style="width: 20%"
            v-for="(hero, i) in category.heroList"
            :key="i"
          >
            <img :src="hero.avatar" alt="" class="w-100" />
            <div>{{ hero.name }}</div>
          </router-link>
        </div>
      </template>
    </m-list-card>

    <m-card title="精彩视频" icon="menu1">
      <template>
        <div class="contain d-flex flex-wrap w-100">
          <div class="p-2" style="width:50%" v-for="(vedio,i) in VedioCats" :key="i">
            <a :href="vedio.items[0].url">
              <img class="w-100"  :src="vedio.items[0].image" alt="" />
              <div><span class="">{{vedio.name}}</span></div>
            </a>  
          </div>
        </div>
      </template>
    </m-card>
  </div>
</template>

<script>
import Swiper, { Navigation, Pagination,Autoplay  } from "swiper";
Swiper.use([Navigation, Pagination,Autoplay]);
import dayjs from "dayjs";
export default {
  filters: {
    date(val) {
      return dayjs(val).format("MM/DD");
    },
  },
  data() {
    return {
      swiperOptions: {
        loop: true, // 循环模式选项
        autoplay: {
          delay: 1000, // 自动切换的时间间隔，单位ms
          stopOnLastSlide: false, // 当切换到最后一个slide时停止自动切换
          disableOnInteraction: false // 用户操作swiper之后，是否禁止autoplay。
        },
        pagination: {
          el: ".pagination-home",
        },
        // Some Swiper option/callback...
      },
      count: true,
      newsCats: [],
      heroCats: [],
      VedioCats: [],
    };
  },
  methods: {
    changeHeight() {
      let nav = document.querySelector(".nav-icons");
      if (this.count) {
        nav.style.height = 71.5 + "px";
        this.count = false;
      } else {
        nav.style.height = 143 + "px";
        this.count = true;
      }
    },
    async fetchNewsCats() {
      const res = await this.$http.get("news/list");

      this.newsCats = res.data;
    },
    async fetchHeroCats() {
      const res = await this.$http.get("heros/list");

      this.heroCats = res.data;
    },
    async fetchVedioCats() {
      const res = await this.$http.get("videos/list");
      this.VedioCats = res.data;
    },
  },
  created() {
    this.fetchNewsCats();
    this.fetchHeroCats();
    this.fetchVedioCats();
  },
};
</script>


